@charset "utf-8";

/* CSS Document */


@font-face {
  font-family: 'acquest_scriptregular';
  src: url('/fonts/acquestscript-webfont.eot');
  src: url('/fonts/acquestscript-webfont.eot?#iefix') format("embedded-opentype"), url('/fonts/acquestscript-webfont.woff') format("woff"), url('/fonts/acquestscript-webfont.ttf') format("truetype");
  font-weight: normal;
  font-style: normal;
}

/*----*****---- << General >> ----*****----*/

body {
  font: normal 16px Calibri, sans-serif;
}

/*----*****---- << Google Fonts >> ----*****----*/

h1, h3, h4, th, .button, input[type=submit] {
  font-family: 'Oswald', sans-serif;
}

.widget.widget_calendar {
  caption, th {
    font-family: 'Oswald', sans-serif;
  }
}

ul.menu li a, .breadcrumb, ul.cat-menu li a, .categories-list ul li a, .post-details, .pagination {
  font-family: 'Oswald', sans-serif;
}

.page-link {
  a, span {
    font-family: 'Oswald', sans-serif;
  }
}

#sorting-container a, .gallery-title h5 a, .tabs ul li a, .accordion li a, a.tooltip, .sticky .post-title .featured {
  font-family: 'Oswald', sans-serif;
}

input {
  &[type=text], &[type=password], &.text {
    font-family: Tahoma, Geneva, sans-serif;
  }
}

textarea, .widget.widget_nav_menu ul.menu li a, #searchform input[type="text"] {
  font-family: Tahoma, Geneva, sans-serif;
}

#footer {
  .widget.social-widget h2 {
    font-family: Tahoma, Geneva, sans-serif !important;
  }

  h2 {
    font-weight: normal !important;
    line-height: 44px !important;
    font-size: 20px;
    text-align: center;
    font-family: Lobster;
    color: #201d1a;
  }
}

.big-ico-button, span.arctext, .notice, .back-btn, table.price-table td.even, #newsletter h2, .theme-default .nivo-caption h2 {
  font-family: 'Niconne', cursive;
}

/*----*****---- << Headings >> ----*****----*/

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

h1 {
  font-size: 26px;
  line-height: 30px;
  margin-bottom: 20px;
  text-transform: uppercase;
}

h2 {
  font-size: 20px;
  line-height: 26px;
  margin-bottom: 15px;
}

h3 {
  font-size: 18px;
  line-height: 20px;
  margin-bottom: 15px;
}

h4 {
  font-size: 16px;
  line-height: 15px;
  margin-bottom: 15px;
}

h5 {
  font-size: 13px;
  line-height: 15px;
  margin-bottom: 10px;
}

h6 {
  font-size: 14px;
  line-height: 15px;
  margin-bottom: 10px;
}

h1.title, h2.title, h3.title, h4.title, h5.title, h6.title {
  float: left;
  width: 100%;
  line-height: 45px;
  text-transform: uppercase;
  padding: 0px 0px 5px;
  margin: 0px 0px 30px;
  font-family: Calibri, sans-serif;
}

h1.title span, h2.title span, h3.title span, h4.title span, h5.title span, h6.title span {
  display: block;
  padding: 0px 10px;
}

h2.title {
  line-height: 40px;
}

h3.title {
  line-height: 35px;
}

h4.title, h5.title, h6.title {
  line-height: 30px;
}

/*----*****---- << Text Elements >> ----*****----*/

a, h1 a:hover, h2 a:hover, h3 a:hover, h4 a:hover, h5 a:hover, h6 a:hover {
  text-decoration: none;
}

a img {
  border: none;
}

strong {
  font-weight: bold;
}

em {
  font-style: italic;
}

p {
  line-height: 20px;
  margin-bottom: 10px;
}

ul, ol, pre {
  margin-bottom: 20px;
}

code {
  margin-bottom: 10px;
}

ul {
  list-style-type: disc;
  list-style-position: inside;

  li {
    padding-bottom: 10px;
    padding-left: 20px;

    ul {
      margin-bottom: 0px;
      list-style-type: disc;
    }
  }

  ul ul {
    margin-bottom: 0px;
    list-style-type: square;
  }
}

ol {
  list-style-type: decimal;
  list-style-position: outside;
  padding-left: 20px;
  padding-top: 10px;

  li {
    padding-bottom: 10px;

    ol {
      margin-bottom: 0px;
      list-style-type: lower-alpha;
    }
  }

  ol ol {
    margin-bottom: 0px;
    list-style-type: lower-roman;
  }
}

blockquote {
  line-height: 20px;
  font-style: italic;
  padding: 15px 10px 20px 70px;
  margin: 10px 0px 10px;
  float: left;

  p {
    margin-bottom: 0px;
  }

  cite {
    float: right;
  }

  &.type2, &.type3 {
    padding-left: 80px;
  }
}

pre, code {
  display: block;
  padding: 10px;
  font-size: 11px;
  font-family: monospace;
  clear: both;
}

hr {
  border: none;
  height: 1px;
  margin: 30px 0;
}

/* due to IE <HR> margin bugs I had to made classed and apply them to div to achieve the effect of a <HR> */

.hr {
  width: 100%;
  height: 6px;
  clear: both;
  float: left;
  margin: 30px 0;
}

.hr_invisible {
  float: left;
  width: 100%;
  margin: 25px 0px;
}

.hr.thin {
  height: 2px;
}

.clear {
  float: none;
  clear: both;
  margin: 0px;
  padding: 0px;
}

/*----*****---- << Tables >> ----*****----*/

table {
  border-spacing: 0;
  border-collapse: collapse;
  border-top-width: 5px;
  margin-bottom: 20px;
  width: 100%;
  clear: both;
}

th {
  padding: 10px 15px;
  font-size: 16px;
}

td {
  padding: 13px 15px;
}

tr.even td {
  background: #d4cc9a;
}

tfoot td {
  background: #d4cc9a;
  border-bottom: 4px solid #9d9564;
}

/*----*****---- << Definition Lists >> ----*****----*/

dl {
  margin: 0px 20px;

  &.gallery-item {
    margin: 0px;
  }
}

.gallery-item img {
  border: 2px solid #617b00 !important;
}

.gallery-caption {
  margin-bottom: 10px;
}

dt {
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 10px;
}

dd {
  margin-bottom: 20px;
  line-height: 20px;
}

/*----*****---- << Buttons >> ----*****----*/

.button {
  color: #fffae4;
  text-transform: uppercase;
  float: left;
  border: none;

  &:hover {
    -webkit-transition: opacity 0.5s;
    -moz-transition: opacity 0.5s;
    -o-transition: opacity 0.5s;
    opacity: 0.8;
    text-decoration: none;
  }

  &.small {
    font-size: 16px;
    padding: 6px 20px 5px;
  }

  &.medium {
    font-size: 18px;
    padding: 9px 20px 8px;
  }

  &.big {
    font-size: 24px;
    padding: 7px 30px 6px;
    overflow: visible;
  }

  &.shape {
    background-image: none !important;
    text-transform: none;
    font-size: 15px;
    padding: 6px 10px 6px 15px;
    position: relative;
    margin-right: 10px;

    span {
      display: block;
      position: absolute;
      left: 100%;
      top: 0px;
      width: 0;
      height: 0;
      border-bottom: 16px solid transparent;
      border-top: 17px solid transparent;
    }

    &.left {
      margin-left: 10px;
      padding: 6px 14px 6px 9px;

      span {
        left: -10px;
        border-left: 0px !important;
      }

      &.black span {
        border-right: 10px solid #252b2e;
      }

      &.green span {
        border-right: 10px solid #859e1a;
      }

      &.red span {
        border-right: 10px solid #a03934;
      }

      &.brown span {
        border-right: 10px solid #806a51;
      }

      &.blue span {
        border-right: 10px solid #3577a0;
      }

      &.orange span {
        border-right: 10px solid #b36b22;
      }

      &.pink span {
        border-right: 10px solid #963a7b;
      }

      &.yellow span {
        border-right: 10px solid #b99942;
      }

      &.cyan span {
        border-right: 10px solid #21b3b3;
      }
    }

    &.black span {
      border-left: 10px solid #252b2e;
    }

    &.green span {
      border-left: 10px solid #859e1a;
    }

    &.red span {
      border-left: 10px solid #a03934;
    }

    &.brown span {
      border-left: 10px solid #806a51;
    }

    &.blue span {
      border-left: 10px solid #3577a0;
    }

    &.orange span {
      border-left: 10px solid #b36b22;
    }

    &.pink span {
      border-left: 10px solid #963a7b;
    }

    &.yellow span {
      border-left: 10px solid #b99942;
    }

    &.cyan span {
      border-left: 10px solid #21b3b3;
    }
  }

  &.black {
    background: #252b2e url(../img/btn-black-bg.jpg) repeat;
    text-shadow: -1px -1px 0px #000000;
  }

  &.green {
    background: #859e1a url(../img/btn-green-bg.jpg) repeat;
    text-shadow: -1px -1px 0px #596a13;
  }

  &.red {
    background: #a03934 url(../img/btn-red-bg.jpg) repeat;
    text-shadow: -1px -1px 0px #6b1313;
  }

  &.brown {
    background: #806a51 url(../img/btn-bg.jpg) repeat;
    text-shadow: -1px -1px 0px #634e37;
  }

  &.blue {
    background: #3577a0 url(../img/btn-blue-bg.jpg) repeat;
    text-shadow: -1px -1px 0px #385363;
  }

  &.orange {
    background: #b36b22 url(../img/btn-orange-bg.jpg) repeat;
    text-shadow: -1px -1px 0px #634e38;
  }

  &.pink {
    background: #963a7b url(../img/btn-pink-bg.jpg) repeat;
    text-shadow: -1px -1px 0px #633756;
  }

  &.yellow {
    background: #b99942 url(../img/btn-yellow-bg.jpg) repeat;
    text-shadow: -1px -1px 0px #635838;
  }

  &.cyan {
    background: #21b3b3 url(../img/btn-cyan-bg.jpg) repeat;
    text-shadow: -1px -1px 0px #386363;
  }
}

.big-ico-button {
  width: 220px;
  height: 80px;
  float: left;
  line-height: 25px;
  font-size: 26px;
  display: table;
  margin: 0px 20px 0px 0px;
  padding: 0px;

  &:hover {
    text-decoration: none;
    -webkit-transition: opacity 0.5s;
    -moz-transition: opacity 0.5s;
    -o-transition: opacity 0.5s;
    opacity: 0.9;
    text-decoration: none;
  }

  span {
    display: table-cell;
    vertical-align: middle;
    padding-left: 90px;
    padding-right: 5px;
  }

  &.red {
    background: url(../img/red-ico-btn.png) no-repeat;
    color: #332521;
    text-shadow: 1px 1px 0px #f3cfbb;
  }

  &.green {
    background: url(../img/green-ico-btn.png) no-repeat;
    color: #fffae4;
    text-shadow: 1px 1px 0px #515c18;
  }

  &.pink {
    background: url(../img/pink-ico-btn.png) no-repeat;
    color: #33212d;
    text-shadow: 1px 1px 0px #f3cfbb;
  }

  &.brown {
    background: url(../img/brown-ico-btn.png) no-repeat;
    color: #332a21;
    text-shadow: 1px 1px 0px #f3cfbb;
  }

  &.red.book span {
    background: url(../img/book-ico-red.png) 10px center no-repeat;
  }

  &.green.book span {
    background: url(../img/book-ico-green.png) 10px center no-repeat;
  }

  &.pink.book span {
    background: url(../img/book-ico-pink.png) 10px center no-repeat;
  }

  &.brown.book span {
    background: url(../img/book-ico-brown.png) 10px center no-repeat;
  }

  &.red.leaf span {
    background: url(../img/leaf-ico-red.png) 10px center no-repeat;
  }

  &.green.leaf span {
    background: url(../img/leaf-ico-green.png) 10px center no-repeat;
  }

  &.pink.leaf span {
    background: url(../img/leaf-ico-pink.png) 10px center no-repeat;
  }

  &.brown.leaf span {
    background: url(../img/leaf-ico-brown.png) 10px center no-repeat;
  }

  &.red.heart span {
    background: url(../img/heart-ico-red.png) 10px center no-repeat;
  }

  &.green.heart span {
    background: url(../img/heart-ico-green.png) 10px center no-repeat;
  }

  &.pink.heart span {
    background: url(../img/heart-ico-pink.png) 10px center no-repeat;
  }

  &.brown.heart span {
    background: url(../img/heart-ico-brown.png) 10px center no-repeat;
  }

  &.red.flower span {
    background: url(../img/flower-ico-red.png) 10px center no-repeat;
  }

  &.green.flower span {
    background: url(../img/flower-ico-green.png) 10px center no-repeat;
  }

  &.pink.flower span {
    background: url(../img/flower-ico-pink.png) 10px center no-repeat;
  }

  &.brown.flower span {
    background: url(../img/flower-ico-brown.png) 10px center no-repeat;
  }

  &.red.star span {
    background: url(../img/star-ico-red.png) 10px center no-repeat;
  }

  &.green.star span {
    background: url(../img/star-ico-green.png) 10px center no-repeat;
  }

  &.pink.star span {
    background: url(../img/star-ico-pink.png) 10px center no-repeat;
  }

  &.brown.star span {
    background: url(../img/star-ico-brown.png) 10px center no-repeat;
  }
}

/*----*****---- << Forms >> ----*****----*/

label {
  font-size: 14px;

  span.required {
    color: #ff0000;
  }
}

fieldset {
  padding: 15px;
  margin: 0 0 20px 0;
}

legend {
  font-weight: bold;
}

input {
  &[type=text], &[type=password], &.text {
    padding: 17px 10px;
    font-weight: normal;
    font-size: 12px;
    width: 94%;
  }
}

textarea {
  padding: 17px 10px;
  font-weight: normal;
  font-size: 12px;
  width: 94%;
}

input {
  &[type=text]:focus, &[type=password]:focus, &.text:focus {
    border-color: #617b00;
  }
}

textarea:focus {
  border-color: #617b00;
}

select {
  padding: 10px;
}

form p {
  margin: 0px;
  line-height: normal;
}

.enquiry-form {
  input {
    &[type=text], &[type=password], &.text {
      float: left;
      clear: both;
    }
  }

  textarea {
    float: left;
    clear: both;
  }

  input[type=submit] {
    float: right;
    clear: both;
  }
}

input[type=submit] {
  background: url(../img/btn-bg.jpg) repeat;
  padding: 4px 17px;
  color: #fffae4;
  text-transform: uppercase;
  text-shadow: -1px -1px 0px #634e37;
  border: none;
  font: normal 18px 'Oswald', sans-serif;
  cursor: pointer;
  margin-top: 10px;
  float: right;
  overflow: visible;
}

.mysubmit {
  background: url(../img/btn-bg.jpg) repeat;
  padding: 4px 17px;
  color: #fffae4;
  text-transform: uppercase;
  text-shadow: -1px -1px 0px #634e37;
  border: none;
  font: normal 18px 'Oswald', sans-serif;
  cursor: pointer;
  margin-top: 10px;
  float: right;
  overflow: visible;

  &:hover {
    background: url(../img/btn-bg-hover.jpg) repeat;
  }
}

input {
  &[type=text], &[type=password], &.text {
    margin: 10px 0;
  }
}

textarea, select {
  margin: 10px 0;
}

textarea {
  width: 94%;
  height: 88px;
  padding: 15px 10px;
}

button {
  border: none;
  cursor: pointer;
}

/*----*****---- << Images >> ----*****----*/

.content img {
  max-width: 100%;
}

.post-content {
  line-height: 20px;

  input[type="submit"] {
    float: none;
  }
}

.wp-caption {
  margin-bottom: 20px;
  max-width: 96%;
  padding: 10px 0px 10px 10px;
  -webkit-transition: all 400ms ease-in-out;
  -moz-transition: all 400ms ease-in-out;
  -o-transition: all 400ms ease-in-out;
  transition: all 400ms ease-in-out;

  img {
    border: none;
  }

  a {
    line-height: 0;
    display: block;
    width: 100%;
  }

  .wp-caption-text {
    text-align: center;
    padding-top: 10px;
    margin: 0px;
  }
}

img {
  &.size-auto, &.size-large, &.size-full, &.size-medium {
    max-width: 99.6%;
    height: auto;
  }
}

.alignleft, img.alignleft {
  display: inline;
  float: left;
  margin-right: 20px;
}

.alignright, img.alignright {
  display: inline;
  float: right;
  margin-left: 20px;
}

.aligncenter, img.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.alignleft, .alignright, .aligncenter, .alignnone {
  margin-bottom: 20px;
}

img {
  &.alignleft, &.alignright, &.aligncenter {
    margin-bottom: 20px;
  }
}

.rounded-img {
  padding: 8px;
  width: 92%;
  height: 92%;
  display: block;
  margin-bottom: 20px;
  -webkit-transition: all 400ms ease-in-out;
  -moz-transition: all 400ms ease-in-out;
  -o-transition: all 400ms ease-in-out;
  transition: all 400ms ease-in-out;
  border-radius: 100%;
  -moz-border-radius: 100%;
  -webkit-border-radius: 100%;

  img {
    display: block;
    width: 100%;
    border-radius: 100%;
    -moz-border-radius: 100%;
    -webkit-border-radius: 100%;
  }
}

.one-fourth .rounded-img {
  border-radius: 204px;
  -moz-border-radius: 204px;
  -webkit-border-radius: 204px;

  img {
    border-radius: 204px;
    -moz-border-radius: 204px;
    -webkit-border-radius: 204px;
  }
}

.green-border, .pink-border {
  display: block;
  line-height: 0;
}

/*----*****---- << Columns >> ----*****----*/

.column {
  margin: 0px 2% 0px 0px;
  float: left;

  &.last {
    margin-right: 0px;
  }
}

.one-fourth {
  width: 23%;
}

.one-half {
  width: 48%;
}

.one-third {
  width: 31.9%;
}

.three-fourth {
  width: 74.5%;
}

.two-third {
  width: 66%;
}

.benefits {
  .one-third {
    width: 26.9%;
  }

  .column {
    margin: 0px 3% 0px 3%;
    float: left;
  }
}

.one-half img, .one-third img, .one-fourth img, .three-fourth img, .two-third img {
  max-width: 100%;
  margin: 0px;
}

.no-margin {
  margin-bottom: 0px;
  margin-right: 0px;

  &.one-third {
    width: 33.2%;
  }

  &.two-third {
    width: 66%;
  }

  &.one-fourth {
    width: 24.9%;
  }

  &.one-half {
    width: 49.9%;
  }
}

/*----*****---- << Lists >> ----*****----*/

ul.flower-bullet {
  clear: both;
  list-style-type: none;
  list-style-position: outside;
  padding: 0px;

  li {
    padding-left: 20px;
    display: block;
    padding-bottom: 10px;

    a {
      text-decoration: underline;
    }
  }

  ul {
    clear: both;
    margin: 15px 0px 0px;

    li:last-child {
      padding-bottom: 5px;
    }
  }

  &.green li {
    background: url(../img/bullet-green.png) left 2px no-repeat;
  }

  &.sandal li {
    background: url(../img/bullet-sandal.png) left 2px no-repeat;
  }

  &.brown li {
    background: url(../img/bullet-brown.png) left 2px no-repeat;
  }

  &.darkbrown li {
    background: url(../img/bullet-dark-brown.png) left 2px no-repeat;
  }

  &.pink li {
    background: url(../img/bullet-pink.png) left 2px no-repeat;
  }

  &.orange li {
    background: url(../img/bullet-orange.png) left 2px no-repeat;
  }

  &.tick li {
    background: url(../img/bullet-tick.png) left top no-repeat;
  }

  &.arrow li {
    background: url(../img/bullet-arrow.png) left top no-repeat;
  }

  &.arrow2 li {
    background: url(../img/bullet-arrow2.png) left top no-repeat;
  }

  &.star li {
    background: url(../img/bullet-star.png) left top no-repeat;
  }
}

/*----*****---- << Widgets >> ----*****----*/

.widget {
  ul li {
    display: block;
    float: left;
    padding-bottom: 12px;
    padding-left: 26px;
    clear: both;
  }

  &.widget_recent_entries {
    ul {
      list-style-type: none;

      li {
        display: block;
        float: left;
        width: 88.4%;
        background: none;
      }
    }

    img {
      float: left;
      margin-right: 13px;
      margin-left: -25px;
      margin-bottom: 20px;
    }

    ul li {
      p {
        margin: 0px;
      }

      &:last-child {
        margin-bottom: 0px;
      }
    }
  }
}

.sidebar .widget.tweetbox ul li, #footer .widget.tweetbox ul li {
  background: none;
  padding-left: 0px;
  line-height: 20px;
  width: 200px;
}

.widget ul {
  margin-bottom: 10px;

  ul {
    margin-top: 20px;
  }
}

.widget_categories ul li {
  position: relative;
  float: none;

  a {
    padding-right: 15px;

    span {
      position: absolute;
      right: 0px;
    }
  }
}

.widget ul.contact-details {
  margin-bottom: 20px;
  float: left;
  width: 100%;

  li {
    margin: 0px;
    display: block;
    clear: both;
    margin-bottom: 10px;
    float: left;
    background: none !important;
    padding: 0px;
  }

  span {
    text-align: center;
    float: left;
    margin: 4px 2% 0px 0px;
