/*----*****---- << Footer >> ----*****----*/

#footer {
  float: left;
  clear: both;
  width: 100%;
  padding-bottom: 5px;

  h2 {
    font-weight: normal;
    line-height: 67px;
    padding: 0px;
    margin: 0px 0px 30px;
    clear: both;

    span {
      display: block;
      padding: 0px 18px;
    }
  }

  a:hover {
    text-decoration: none;
  }

  h6 a {
    font-weight: bold;
  }

  .widget ul {
    padding: 0px;
    list-style-position: outside;
    list-style-type: none;

    li a {
      text-decoration: none;
    }
  }
}

.widget .testimonial-carousel li {
  clear: none;
}

.testimonial-skin-carousel {
  .jcarousel-container {
    width: 300px;
  }

  .jcarousel-container-horizontal {
    width: 300px;
    padding: 0px;
  }

  .jcarousel-clip {
    overflow: hidden;
  }

  .jcarousel-clip-horizontal {
    width: 302px;
    height: auto;
  }

  .jcarousel-item {
    width: 300px;
    height: auto;
  }

  .jcarousel-item-horizontal {
    margin-right: 20px;
  }

  .jcarousel-direction-rtl .jcarousel-item-horizontal {
    margin: 10px;
  }

  .jcarousel-item-placeholder {
    color: #000;
  }
}

.testimonial-nav {
  margin: 20px 0px 0px;
  clear: both;
  float: left;
  width: 100%;
}

.testimonial-skin-carousel {
  .jcarousel-prev-horizontal {
    width: 22px;
    height: 22px;
    float: left;
    margin: 10px 0px 0px;
    cursor: pointer;

    &:hover {
      background-position: left -22px;
    }
  }

  .jcarousel-next-horizontal {
    width: 22px;
    height: 22px;
    float: right;
    margin: 10px 0px 0px;
    cursor: pointer;

    &:hover {
      background-position: -22px -22px;
    }
  }

  .jcarousel-next-disabled, .jcarousel-prev-disabled {
    opacity: 0.4;
    filter: alpha(opacity = 40);
    cursor: auto;
  }
}

#footer {
  .widget .testimonial-skin-carousel ul li {
    width: 290px;
    background: none;
    padding: 5px;
  }

  blockquote {
    float: left;
    background: none;
    border: none;
    padding: 0px;
    margin-top: 0px;

    cite {
      float: right;
      clear: both;
      margin: 25px 0px 0px;
    }
  }
}

.widget.social-widget li {
  position: relative;
  overflow: hidden;
  width: 31px;
  height: 31px;
  background: none;
  padding-left: 0px;
  padding-bottom: 10px;
  clear: none;
  margin: 5px 8px 0px 0px;
  display: inline;
  float: left;
}

.social-widget li {
  img {
    top: 0px;
    left: 0px;
    position: absolute;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;

    &:first-child {
      opacity: 0;
      filter: alpha(opacity = 0);
      z-index: 100;
    }
  }

  &:hover img:first-child {
    opacity: 1;
    filter: alpha(opacity = 100);
    transition: opacity .5s ease-in-out;
    -moz-transition: opacity .5s ease-in-out;
    -webkit-transition: opacity .5s ease-in-out;
  }
}

#footer .widget.social-widget li, .sidebar .widget.social-widget li {
  width: 31px;
  background: none;
  padding: 0px;
}

#footer .widget.social-widget h2 {
  font-weight: bold;
  background: none;
  padding: 0px;
  font-size: 12px;
  line-height: normal;
  height: auto;
  margin-bottom: 10px;

  span {
    background: none;
    padding: 0px;
    height: auto;
  }
}

.footer-bottom {
  width: 100%;
  float: left;
  padding: 20px 0px;
  box-shadow: 0px -1px 6px #0c0c0c;
  -moz-box-shadow: 0px -1px 6px #0c0c0c;
  -webkit-box-shadow: 0px -1px 6px #0c0c0c;

  p {
    margin: 0px;
    font-family: "Lucida Sans Unicode", "Lucida Grande", sans-serif;
  }
}

#footer .widget {
  ul.contact-details {
    margin-bottom: 0px;
  }

  padding-bottom: 30px;
  float: left;
  width: 100%;

  &.tweetbox ul li a:hover {
    text-decoration: underline;
  }
}

